import fetch from "node-fetch";
import config from "./config";
import convert from "../utils/convert";

export default class etherscan {
	static async getTxs() {
		const url = `https://api.etherscan.io/api?module=account&action=txlist&address=${config.address()}&startblock=6604918&endblock=99999999&sort=asc&apikey=${config.apikey()}`;
		const txs = (await (await fetch(url)).json()).result;
		let numberOfBets = 0;
		let totalFunds = 0;
		for (var j = 0; j < txs.length; j++) {
			const v = parseFloat(convert.eth(txs[j].value));
			if (v > 0) {
				numberOfBets++;
			}
			totalFunds += v;
		}

		let usdFunds = totalFunds * config.usdrate();
        totalFunds = parseFloat(totalFunds.toFixed(1));
		usdFunds = parseFloat(usdFunds.toFixed(1));

		return { numberOfBets, totalFunds, usdFunds };
	}
}
